.chart{
    display: grid;
    grid-template-columns: 1fr;
    background: $white;
    padding: 1.5rem;
    row-gap: 1rem;
    border-radius: 10px;
    width: inherit;

    &-top >h1{
        font-size: 28px;
        color: $Dark-brown;

        @media(max-width: 37.5rem){
           font-size: 18px; 
        }

    }

    &-but{
        display: flex;
        justify-content: space-between;
        align-items:last baseline;
        border-top: 1px solid $Cream;
        padding-top: 2rem;

        &_left{
            font-size: 12px;
            color: $Medium-brown;

            & > h2{
                font-size: 30px;
                color: $Dark-brown;
            }
        }

        &_right{
            font-size: 12px;
            color: $Medium-brown;
            display: grid;
            text-align: right;

            &>h3{
                color: $Dark-brown;
            }
        }
    }
}